import React from 'react';
import { Row, Col, Form, Button } from 'react-bootstrap';
import { Formik } from 'formik';
import * as Yup from 'yup';

const SongForm = props => {
	const validation = Yup.object().shape({
		artista: Yup.string()
			.min(3, 'Favor de capturar el nombre real del artista.')
			.required('este campo es requerido.'),
		album: Yup.string()
			.min(2, 'Favor de capturar el nombre del album.')
			.required('este campo es requerido.'),
		cancion: Yup.string()
			.min(2, 'Favor de capturar el nombre del cancion.')
			.required('este campo es requerido.'),
		creditos: Yup.string()
			.min(2, 'Favor de capturar en que estuvo involucrado en el proyecto.')
			.required('este campo es requerido.'),
		enlaceVideo: Yup.string()
			.min(5, 'Escriba el enlace donde se encuentra el video.')
			.required('este campo es requerido.'),
		enlaceImagen: Yup.string()
			.min(5, 'Escriba el enlace donde se encuentra la imagen.')
			.required('este campo es requerido.')
	});

	const form = ({
		handleSubmit,
		handleChange,
		handleBlur,
		values,
		errors,
		touched
	}) => {
		return (
			<Form onSubmit={handleSubmit} className='songForm'>
				<Row>
					<Col sm={12} md={{ span: 6, offset: 3 }}>
						<Form.Group controlId='artista'>
							<Form.Label>Artista</Form.Label>
							<Form.Control
								type='text'
								name='artista'
								onChange={handleChange}
								onBlur={handleBlur}
								value={values.artista}
							/>
							{touched.artista && errors.artista ? (
								<Form.Text className='text-muted'>
									{errors.artista}
								</Form.Text>
							) : null}
						</Form.Group>
					</Col>
				</Row>
				<Row>
					<Col sm={12} md={{ span: 6, offset: 3 }}>
						<Form.Group controlId='album'>
							<Form.Label>Album</Form.Label>
							<Form.Control
								type='text'
								name='album'
								onChange={handleChange}
								onBlur={handleBlur}
								value={values.album}
							/>
							{touched.album && errors.album ? (
								<Form.Text>{errors.album}</Form.Text>
							) : null}
						</Form.Group>
					</Col>
				</Row>
				<Row>
					<Col sm={12} md={{ span: 6, offset: 3 }}>
						<Form.Group controlId='cancion'>
							<Form.Label>Canción</Form.Label>
							<Form.Control
								type='text'
								name='cancion'
								onChange={handleChange}
								onBlur={handleBlur}
								value={values.cancion}
							/>
							{touched.cancion && errors.cancion ? (
								<Form.Text>{errors.cancion}</Form.Text>
							) : null}
						</Form.Group>
					</Col>
				</Row>
				<Row>
					<Col sm={12} md={{ span: 6, offset: 3 }}>
						<Form.Group controlId='creditos'>
							<Form.Label>Créditos</Form.Label>
							<Form.Control
								type='text'
								name='creditos'
								onChange={handleChange}
								onBlur={handleBlur}
								value={values.creditos}
							/>
							{touched.creditos && errors.creditos ? (
								<Form.Text>{errors.creditos}</Form.Text>
							) : null}
						</Form.Group>
					</Col>
				</Row>
				<Row>
					<Col sm={12} md={{ span: 6, offset: 3 }}>
						<Form.Group controlId='enlaceVideo'>
							<Form.Label>Enlace del video</Form.Label>
							<Form.Control
								type='text'
								name='enlaceVideo'
								onChange={handleChange}
								onBlur={handleBlur}
								value={values.enlaceVideo}
							/>
							{touched.enlaceVideo && errors.enlaceVideo ? (
								<Form.Text>{errors.enlaceVideo}</Form.Text>
							) : null}
						</Form.Group>
					</Col>
					<Col sm={12} md={{ span: 6, offset: 3 }}>
						<Form.Group controlId='enlaceImagen'>
							<Form.Label>Enlace de la imagen</Form.Label>
							<Form.Control
								type='text'
								name='enlaceImagen'
								onChange={handleChange}
								onBlur={handleBlur}
								value={values.enlaceImagen}
							/>
							{touched.enlaceImagen && errors.enlaceImagen ? (
								<Form.Text>{errors.enlaceImagen}</Form.Text>
							) : null}
						</Form.Group>
					</Col>
				</Row>
				<Row>
					<Col sm={12} md={{ span: 6, offset: 3 }}>
						<Button variant='primary' type='submit'>
							Enviar
						</Button>
					</Col>
				</Row>
			</Form>
		);
	};

	return (
		<Formik
			initialValues={props.initialValues}
			validationSchema={validation}
			onSubmit={(values, { setFieldError }) => {
				try {
					props.onSubmit(values);
				} catch (errors) {
					console.log(errors);
					errors.forEach(err => {
						setFieldError(err.field.error);
					});
				}
			}}
			component={form}
		/>
	);
};

export default SongForm;
