import { SHOW_MODAL } from '../actions/types';

const INITIAL_STATE = {
	songValues: {
		album: '',
		artista: '',
		cancion: '',
		imagen: '',
		video: '',
		creditos: '',
		show: false
	}
};

export default (state = INITIAL_STATE, action) => {
	switch (action.type) {
		case SHOW_MODAL:
			return { ...state, songValues: action.payload };

		default:
			return state;
	}
};
