import React from 'react';
import { Button } from 'react-bootstrap';

import { connect } from 'react-redux';
import { signIn, signOut } from '../actions';

class GoogleAuth extends React.Component {
	componentDidMount() {
		window.gapi.load('client:auth2', () => {
			window.gapi.client
				.init({
					clientId:
						'323875244275-j227g0em47909uat5u27qpc3pcde1ha9.apps.googleusercontent.com',
					scope: 'email'
				})
				.then(() => {
					this.auth = window.gapi.auth2.getAuthInstance();
					this.onAuthChange(this.auth.isSignedIn.get());
					this.auth.isSignedIn.listen(this.onAuthChange);
				});
		});
	}

	onAuthChange = isSignedIn => {
		if (isSignedIn) {
			this.props.signIn(this.auth.currentUser.get().getId());
		} else {
			this.props.signOut();
		}
	};

	onSignInClick = () => {
		this.auth.signIn();
	};

	onSignOutClick = () => {
		this.auth.signOut();
	};

	renderAuthButton() {
		if (this.props.isSignedIn === null) {
			return null;
		} else if (this.props.isSignedIn) {
			return (
				<Button variant='danger' onClick={this.onSignOutClick}>
					<i className='fab fa-google mr-2'></i>
					Cerrar sesión
				</Button>
			);
		} else {
			return (
				<Button variant='success' onClick={this.onSignInClick}>
					<i className='fab fa-google mr-2'></i>
					Iniciar sesión
				</Button>
			);
		}
	}

	render() {
		return <React.Fragment>{this.renderAuthButton()}</React.Fragment>;
	}
}

const mapStateToProps = state => {
	return { isSignedIn: state.auth.isSignedIn };
};

export default connect(mapStateToProps, { signIn, signOut })(GoogleAuth);
