import fondo from '../assets/img/f03.jpg';

import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import { connect } from 'react-redux';
import { fetchSongs } from '../actions';

import SongComponent from './songs/SongComponent';

class Portfolio extends React.Component {
	state = { showModal: false };

	componentDidMount() {
		this.props.fetchSongs();
	}

	componentWillReceiveProps(nextProps) {
		if (nextProps.modal.show !== this.state.showModal) {
			this.setState({ showModal: true });
		}
	}

	toggleClass = () => {
		const currentState = this.state.showModal;
		this.setState({ showModal: !currentState });
	};

	renderModal = () => {
		return (
			<div
				className={`popup ${this.state.showModal ? '' : 'd-none'}`}
				onClick={this.toggleClass}>
				<div className='popup__content' onClick={e => e.stopPropagation()}>
					<div className='popup__left'>
						<img
							src={this.props.modal.imagen}
							alt={this.props.modal.cancion}
						/>
					</div>
					<div className='popup__right'>
						<div className='popup__close' onClick={this.toggleClass}>
							&times;
						</div>
						<h2 className='popup__title'>{this.props.modal.cancion}</h2>
						<h3 className='popup__subtitle'>
							{this.props.modal.artista}
						</h3>
						<ul className='popup__list'>
							<li className='popup__item'>
								<label>Album:</label>
								{this.props.modal.album}
							</li>
							<li className='popup__item'>
								<label>Créditos:</label>
								{this.props.modal.creditos}
							</li>
						</ul>
						<a
							href={this.props.modal.video}
							target='_blank'
							rel='noopener noreferrer'
							className='btn btn__color'>
							Ver video
						</a>
					</div>
				</div>
			</div>
		);
	};

	renderList = () => {
		const listSongs = this.props.songs.sort((a, b) => {
			return new Date(a.fecha) - new Date(b.fecha);
		});

		return listSongs.map(song => {
			return (
				<SongComponent key={song.id} {...song} onClick={this.renderModal} />
			);
		});
	};

	render() {
		document.body.style.backgroundImage = `url(${fondo})`;

		return (
			<React.Fragment>
				{this.renderModal()}
				<Container fluid={false} className='porfolio'>
					<Row className='my-4'>
						<Col>
							<h3 className='heading__three'>Portafolio</h3>
						</Col>
					</Row>
					<section className='portfolio__grid u-lines'>
						{this.renderList()}
					</section>
				</Container>
			</React.Fragment>
		);
	}
}

const mapStateToPros = state => {
	return { songs: Object.values(state.song), modal: state.modal.songValues };
};

export default connect(mapStateToPros, { fetchSongs })(Portfolio);
