import React from 'react';

import { connect } from 'react-redux';
import { showModal } from '../../actions';

class SongComponent extends React.Component {
	renderModal = () => {
		this.props.showModal({
			album: this.props.album,
			artista: this.props.artista,
			cancion: this.props.cancion,
			imagen: this.props.enlaceImagen,
			video: this.props.enlaceVideo,
			creditos: this.props.creditos,
			show: true
		});
	};

	renderSong = () => {
		return (
			<article className='song' onClick={this.renderModal}>
				<img
					className='song__image'
					src={this.props.enlaceImagen}
					alt={this.props.cancion}
				/>
				<div className='song__content'>
					<span>{this.props.cancion}</span>
					<span>{this.props.artista}</span>
				</div>
			</article>
		);
	};
	render() {
		return <React.Fragment>{this.renderSong()}</React.Fragment>;
	}
}

export default connect(null, { showModal })(SongComponent);
