import React from "react";
import { Link } from "react-router-dom";
import { Button } from "react-bootstrap";
import Modal from "../Modal";
import history from "../../history";
import { connect } from "react-redux";
import { fetchSong, deleteSong } from "../../actions";

class SongDelete extends React.Component {
  componentDidMount() {
    this.props.fetchSong(this.props.match.params.id);
  }

  renderActions() {
    const id = this.props.match.params.id;
    return (
      <React.Fragment>
        <Button variant="danger" onClick={() => this.props.deleteSong(id)}>
          Eliminar
        </Button>
        <Link to="/song/list">
          <Button variant="secondary">Cancelar</Button>
        </Link>
      </React.Fragment>
    );
  }

  renderContainer() {
    if (!this.props.songSelected) {
      return <p>Loading...</p>;
    }
    return (
      <React.Fragment>
        <h3 className="popup__subtitle">{this.props.songSelected.artista}</h3>
        <ul className="popup__list">
          <li className="popup__item">
            Album: <span>{this.props.songSelected.album}</span>
          </li>
          <li className="popup__item">
            Canción: <span>{this.props.songSelected.cancion}</span>
          </li>
        </ul>
      </React.Fragment>
    );
  }

  renderImagen() {
    return (
      <img
        src={this.props.songSelected.enlaceImagen}
        alt=""
        className="popup__img"
      />
    );
  }

  render() {
    return (
      <Modal
        imagen={this.renderImagen()}
        title="¿Desea eliminar esta canción?"
        content={this.renderContainer()}
        actions={this.renderActions()}
        onDismiss={() => history.push("/song/list")}
      />
    );
  }
}

const mapStateToProps = (state, ownProps) => {
  return { songSelected: state.song[ownProps.match.params.id] };
};

export default connect(mapStateToProps, { fetchSong, deleteSong })(SongDelete);
