import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import { connect } from 'react-redux';
import { createSong } from '../../actions';

import SongForm from './SongForm';

class SongCreate extends React.Component {
	onSubmit = formValues => {
		this.props.createSong(formValues);
	};

	getDate() {
		const fecha = new Date();
		const hour = (fecha.getHours() < 10 ? '0' : '') + fecha.getHours();
		const minute = (fecha.getMinutes() < 10 ? '0' : '') + fecha.getMinutes();
		const second = (fecha.getSeconds() < 10 ? '0' : '') + fecha.getSeconds();

		return `${fecha.getFullYear()}-${fecha.getMonth() +
			1}-${fecha.getDate()}T${hour}:${minute}:${second}`;
	}

	render() {
		return (
			<Container fluid={false}>
				<Row>
					<Col>
						<h3>Nueva canción</h3>
					</Col>
				</Row>
				<SongForm
					initialValues={{
						artista: '',
						album: '',
						cancion: '',
						creditos: '',
						enlaceVideo: 'https://www.youtube.com/',
						enlaceImagen: '',
						id: '',
						fecha: this.getDate()
					}}
					onSubmit={this.onSubmit}
				/>
			</Container>
		);
	}
}

export default connect(null, { createSong })(SongCreate);
