import mailSender from "../apis/mailSender";
import firebase from "../apis/firebase";
import {
  SEND_CONTACT,
  FETCH_SONGS,
  FETCH_SONG,
  CREATE_SONG,
  EDIT_SONG,
  DELETE_SONG,
  SIGN_IN,
  SIGN_OUT,
  SHOW_MODAL
} from "./types";
import history from "../history";

export const formSender = formValues => async dispatch => {
  const response = await mailSender.post("", formValues);
  dispatch({ type: SEND_CONTACT, payload: response.ok });
};

export const fetchSongs = () => async dispatch => {
  const updateApi = JSON.parse(localStorage.getItem("updateApi"));
  const fecha = new Date(getHoy(0));
  const maniana = new Date(getHoy(1));
  let response = null;

  if (!updateApi) {
    response = await firebase.get("/catalogos.json");
    localStorage.setItem("updateApi", JSON.stringify(maniana));
    localStorage.setItem("sonicApi", JSON.stringify(response));
  } else {
    const apiDate = new Date(updateApi);
    const difference = fecha.getTime() - apiDate.getTime();

    if (difference > 0) {
      response = await firebase.get("/catalogos.json");
      localStorage.setItem("updateApi", JSON.stringify(maniana));
      localStorage.setItem("sonicApi", JSON.stringify(response));
    } else {
      response = JSON.parse(localStorage.getItem("sonicApi"));
    }
  }

  dispatch({ type: FETCH_SONGS, payload: response.data });
};

export const fetchSong = idCancion => async dispatch => {
  const response = await firebase.get(`/catalogos/${idCancion}.json`);
  dispatch({ type: FETCH_SONG, payload: response.data });
};

export const createSong = formValues => async dispatch => {
  formValues.id = Math.random()
    .toString(36)
    .substr(2, 9);
  const idCancion = formValues.id;
  const response = await firebase.put(
    `/catalogos/${idCancion}.json`,
    formValues
  );
  dispatch({ type: CREATE_SONG, payload: response.data });
  history.push("/song/list");
};

export const editSong = formValues => async dispatch => {
  const idCancion = formValues.id;
  const response = await firebase.patch(
    `/catalogos/${idCancion}.json`,
    formValues
  );
  dispatch({ type: EDIT_SONG, payload: response.data });
  history.push("/song/list");
};

export const deleteSong = idCancion => async dispatch => {
  await firebase.delete(`/catalogos/${idCancion}.json`);
  dispatch({ type: DELETE_SONG, payload: idCancion });
  history.push("/song/list");
};

export const signIn = userId => {
  return {
    type: SIGN_IN,
    payload: userId
  };
};

export const signOut = () => {
  return {
    type: SIGN_OUT
  };
};

export const showModal = songValues => {
  return {
    type: SHOW_MODAL,
    payload: songValues
  };
};

const getHoy = num => {
  const fecha = new Date();
  const hour = (fecha.getHours() < 10 ? "0" : "") + fecha.getHours();
  const minute = (fecha.getMinutes() < 10 ? "0" : "") + fecha.getMinutes();
  const second = (fecha.getSeconds() < 10 ? "0" : "") + fecha.getSeconds();

  return `${fecha.getFullYear()}-${fecha.getMonth() + 1}-${fecha.getDate() +
    num}T${hour}:${minute}:${second}`;
};
